import { Notify } from 'notiflix/build/notiflix-notify-aio';
// import { KEY } from './constants';
import { refs } from './refs';
import ServerRequest from './serverRequest';
import posterDefault from '../images/posterMovie.jpg';

const TRENDING_LIST = 'trending/movie/week'; //    
const BASE_URL_IMG = 'https://image.tmdb.org/t/p/w500'; //    ,  

export const movieAPI = new ServerRequest(TRENDING_LIST); //       .             (     !!!!!!),   

renderPageMovies(movieAPI); //          !!!      

export async function renderPageMovies(requestAPI = movieAPI) { //      
  try {
    const { results } = await requestAPI.getMovies(); //     

    const markup = await renderMovieCards(results); //    

    refs.galleryList.innerHTML = markup.join(''); //    DOM
  } catch (error) {
    Notify.failure(error.message);
  }
}

export async function renderMovieCards(movies) {
  const genres = await movieAPI.getGenres(); //    
  //         
  return movies.map(({ title, poster_path, release_date, genre_ids, id }) => {
    const poster = poster_path ? BASE_URL_IMG + poster_path : posterDefault; //   ,         
    const releaseYear = release_date.split('').slice(0, 4).join(''); //       
    //     
    const genresName = genres
      .filter(genre => genre_ids.includes(genre.id))
      .map(el => el.name);

    if (genresName.length > 2) {  //        2-
      genresName.splice(2, genresName.length, 'Other'); //        "Other"
    }

    return `<li class="gallery__item" data-movieID="${id}">
              <img src="${poster}" alt="${title}" class="gallery__item-poster">
               <div class=" gallery__item-descr-wrap">
                 <h2 class="gallery__item-title">${title}</h2>
                 <p class="gallery__item-genre">${genresName.join(', ')} <span class="gallery__item-date">| ${releaseYear}</span></p>
              </div>
            </li>`;
  });
}